.card {
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.15);
  width: 100%;
  cursor: pointer;
  position: relative;
  border-radius: 0;
  border: none;
  border-radius: 6px;
  margin-bottom: 1.5rem;
  height: 340px;
  overflow-y: hidden;
  transition: transform 0.5s cubic-bezier(0.6, 0, 0.45, 1.3),
    width 0.5s cubic-bezier(0.6, 0, 0.45, 1.3);

  & > div {
    height: 100vh;
  }

  &.is-open {
    width: 100vw;
    height: 100vh;
    border-radius: 0;
    z-index: 999;
    border: 0;
    overflow-y: initial;
    .card__body {
      bottom: 340px;
    }
  }

  &__body {
    z-index: 2;
    position: absolute;
    padding: 1rem;
    bottom: 0;
    color: #fff;
    max-width: 100%;
    max-height: 225px;
  }

  &__desc {
    max-width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__content {
    background-color: #fff;
    color: darkslategray;
    position: relative;
    height: 100%;
    top: 340px;
    padding: 1rem;
    overflow: auto;
  }

  &__overlay {
    content: "";
    position: absolute;
    width: 100%;
    height: 340px;
    background-color: rgba(0, 0, 0, 0.3);
    top: 0;
    left: 0;
    z-index: 2;
    border-radius: 6px;
  }

  img,
  video {
    border-radius: 6px;
    width: 100%;
    height: 340px;
    object-fit: cover;
    position: absolute;
    left: 0;
    top: 0;
  }
}
